<template>
  <div class="chapter-details">
  <div class="top">
      <div class="back-btn" @click="back"></div>
    </div>
    <div class="chapte-name">
      <input type="text" placeholder="  " v-model="cname">
    </div>
    <quill-editor 
        v-model="info" 
        ref="myQuillEditor" 
        :options="editorOption" 
        
        >
    </quill-editor>
    <div class="btn" @click="submit"></div>
     
  </div>
</template>
<script>
import axios from 'axios'
import httpx from '../assets/httpx.js'
const CryptoJS = require('crypto-js');  //AESjs    

export default {
  data(){
    return{
      bid:"",
      cid:"",
      cname:"",
      info:"",
      editorOption:{},
    }
  },
  methods:{
    back(){
      this.$router.back();
    },
    //
    decryptContent(url, key) {
      let that=this;
      var oReq = new XMLHttpRequest();
      oReq.open("GET", url, true);
      oReq.responseType = "arraybuffer";
      oReq.onload = function (oEvent) {
        var arrayBuffer = oReq.response;
        if (arrayBuffer) {
            var ciphertext = CryptoJS.lib.WordArray.create(new Uint8Array(arrayBuffer));
            var iv = ciphertext.clone();
            iv.sigBytes = 16;
            iv.clamp();
            ciphertext.words.splice(0, 4); // delete 4 words = 16 bytes
            ciphertext.sigBytes -= 16;
            key = CryptoJS.enc.Utf8.parse(key);
            var decrypted = CryptoJS.AES.decrypt({ciphertext: ciphertext}, key, {
                iv: iv,
                mode: CryptoJS.mode.CBC,
                padding: CryptoJS.pad.Pkcs7
            });
            var content = decrypted.toString(CryptoJS.enc.Utf8);
            that.info=content;
            console.log(that.info,132)
        } else {
            console.error('error');
        }
      };
      oReq.send(null);
    },
    //
    getdata(){
      let token=sessionStorage.getItem("token");
        if(token==""||token==undefined){
        this.$router.push("/")
      }
      this.bid=sessionStorage.getItem("bid");
      this.cid=sessionStorage.getItem("cid");
      this.cname=sessionStorage.getItem("title");
      let url=sessionStorage.getItem("clink");
      console.log(url.slice(0,-13));
      let clink="https://sk0001.oss-cn-beijing.aliyuncs.com/"+url.slice(0,-13)+`${this.cid}.txt`
      const key ="EC+E9ZqZwNjAm6T4g/tVEQthu6aSxNRu";  //
      this.decryptContent(clink, key);
    },
    //

    submit(){
      console.log(this.info,132)
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://interfa.tuxiansheng.top/api";
      let data={
        "action": "mod_chapter",
        "data": {
          "bid": parseInt(this.bid),
          "idx": parseInt(this.cid),
          "title":this.cname,
          "content":this.info
        }
      }
      axios({
        method: 'post',
        url: url,
        timeout:600000,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,
          "Access-Token":token,             
          'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("")
          this.$router.push("/");
        }
        console.log(res.data)
        if(res.data.errcode==0){
          this.$message({
            message: '',
            type: 'success'
          });
          this.bookmsg=res.data.data;
          sessionStorage.setItem("title",this.cname);
        }else{
          this.$message.error(res.data.errmsg);
        }
      })
    },
    onEditorReady(editor) { // 
    },
    onEditorBlur(){}, // 
    onEditorFocus(){}, // 
    onEditorChange(){}, // 
  },
  computed: {
    editor() {
        return this.$refs.myQuillEditor.quill;
    },
  },
  created(){
    this.getdata();
  },
  mounted() {
    
  },
}
</script>
<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li,textarea{
  margin:0px;
  padding:0px;
}
.chapter-details{
  box-sizing:border-box;
  padding:0px 32px;
}
.chapter-details .top{
  width: 100%;
  padding: 14px 0px 21px 0px;
}
.chapter-details .top .back-btn{
  width: 80px;
  height: 30px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  line-height: 30px;
  cursor: pointer;
}
.chapter-details .chapte-name{
  margin-bottom: 20px;
}
.chapter-details .chapte-name input{
  width: 485px;
  height: 33px;
  color: rgba(136, 136, 136, 1);
  font-size: 14px;
  text-align: left;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  padding-left:8px;
  border-radius: 5px;
  outline: none;
}
.chapter-details .content textarea{
  width: 726px;
  height: 299px;
  line-height: 20px;
  color: rgba(136, 136, 136, 1);
  font-size: 14px;
  text-align: left;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  resize:none;
}
.chapter-details .btn{
  width: 101px;
  height: 30px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  line-height: 30px;
  margin:22px auto;
  cursor: pointer;
}
</style>


